import { onAuthStateChanged } from "firebase/auth";
import React, { useEffect, useState } from "react";
import { Button, Container, Nav, Navbar } from "react-bootstrap";
import AuthUser from "./components/AuthUser";
import UserAvatar from "./components/UserAvatar";
import { auth } from "./services/firbaseConfig";

const App = () => {
  const [user, setUser] = useState(null);
  const [isDisabled, setIsDisabled] = useState(true);
  const [registered, setRegistered] = useState(false);

  useEffect(() => {
    onAuthStateChanged(auth, (user) => {
      if (user) {
        setUser(user);
      } else {
        setUser(null);
        setIsDisabled(false);
        setRegistered(false);
      }
    });
  });

  const userInfo = () => {
    return user !== null ? (
      <UserAvatar />
    ) : (
      <Button
        variant="secondary"
        onClick={handleRegister}
        disabled={isDisabled}
      >
        REGISTER
      </Button>
    );
  };

  const handleRegister = () => {
    console.log("reg");
  };

  return (
    <Container
      fluid
      className="p-0"
      style={{
        height: "100vh",
        width: "100vw",
      }}
    >
      <Navbar as="nav" bg="light" expand="sm" variant="light" sticky="top">
        <Container>
          <Navbar.Brand href="#home">0NLY T3XT</Navbar.Brand>
          <Nav className="ms-auto p-1 pt-2">
            <Nav.Item>{userInfo()}</Nav.Item>
          </Nav>
        </Container>
      </Navbar>
      <Container fluid="md" className="py-3" as="main">
        {!registered && (
          <AuthUser
            user={user}
            setRegistered={() => setRegistered(true)}
            registered={registered}
          />
        )}
      </Container>
    </Container>
  );
};

export default App;
