import { child, get, ref } from "firebase/database";
import React, { useEffect, useState } from "react";
import { Button, Card, Container } from "react-bootstrap";
import { db } from "../../services/firebaseServe";

const FinalProfile = ({ user, setRegistered, setPage }) => {
  const [userInfo, setUserinfo] = useState({
    userid: user.email,
    email: "",
  });

  useEffect(() => {
    get(child(ref(db), `users/${user.uid}`)).then((snap) => {
      if (snap.exists()) setUserinfo((prev) => ({ ...prev, ...snap.val() }));
      else console.log("not found");
    });
  }, [user]);

  return (
    <Container fluid className="w-100">
      <Card
        className="w-100 mx-auto shadow"
        style={{ minWidth: "10em", maxWidth: "18em" }}
      >
        <Card.Img
          variant="top"
          className="ratio ratio-1x1"
          src={user.photoURL}
          referrerPolicy="no-referrer"
        />
        <Card.Body className="text-center">
          <Card.Title style={{ fontSize: "1.4em" }}>
            {userInfo.userid}
          </Card.Title>
          <Card.Text>{userInfo.email}</Card.Text>
          <Button variant="success" onClick={() => setRegistered(true)}>
            {"Start Texting >"}
          </Button>
        </Card.Body>
      </Card>
    </Container>
  );
};

export default FinalProfile;
