import React, { useEffect, useState } from "react";
import { Container, ProgressBar, Spinner } from "react-bootstrap";
import ErrorPage from "./ErrorPage";
import DetailsForm from "./registration/DetailsForm";
import FinalProfile from "./registration/FinalProfile";
import SignUp from "./registration/SignUp";

const AuthUser = ({ user, setRegistered, registered }) => {
  const [current, setCurrent] = useState("signup");
  const [isLoading, setIsLoading] = useState(true);
  const [userInfo, setUserInfo] = useState({
    email: "example@mail.com",
    userid: "Jon Doe",
    photoURL: "gs://only-text-5ec40.appspot.com/images/woman.png",
  });

  useEffect(() => {
    if (user === null) setCurrent("signup");
    else setUserInfo(user);
  }, [user]);

  const getprogress = () => {
    if (current === "signup") return 1;
    else if (current === "details") return 2;
    else if (current === "final") return 3;
    else return 0;
  };

  console.log("auth user");

  const loading = () => {
    return (
      <>
        <Container
          fluid
          className="position-absolute w-100 h-100 top-0 start-0 end-0 align-items-center bg-dark opacity-25"
        ></Container>
        <Spinner
          className="top-50 start-50 position-absolute"
          animation="border"
          style={{
            zIndex: 2,
          }}
        />
      </>
    );
  };

  const tabChooser = () => {
    if (current === "signup") {
      return (
        <SignUp
          setPage={(page) => setCurrent(page)}
          user={userInfo}
          setIsLoading={setIsLoading}
        />
      );
    } else if (current === "details") {
      return (
        <DetailsForm
          setPage={(page) => setCurrent(page)}
          user={userInfo}
          setIsLoading={setIsLoading}
        />
      );
    } else if (current === "final") {
      return (
        <FinalProfile
          user={userInfo}
          setRegistered={setRegistered}
          setPage={(page) => setCurrent(page)}
        />
      );
    } else return <ErrorPage />;
  };

  return (
    <Container
      fluid="sm"
      className="bg-light m-2 py-3 px-2 mx-auto position-relative"
      style={{
        maxWidth: "55em",
      }}
    >
      <ProgressBar
        className="mb-3"
        now={(getprogress() / 3) * 100}
        variant="secondary"
        style={{ height: "0.7em" }}
      />
      {tabChooser()}
      {isLoading && loading()}
    </Container>
  );
};

export default AuthUser;
