import React from "react";
import { Button } from "react-bootstrap";
import { googleSignOut } from "../services/firebaseAuth";

const UserAvatar = () => {
  const handleSignOut = () => {
    googleSignOut()
      .then(() => {
        console.log("logged out");
      })
      .catch();
  };
  return (
    <Button variant="danger" onClick={handleSignOut}>
      SIGN OUT
    </Button>
  );
};

export default UserAvatar;
